import { Fragment, useEffect, useState } from "react";
import { Link } from "react-scroll";
import "./NavBar.css"

const NavBar = () => {

    const [showBG, setShowBG] = useState(false)
    const scrollTime = 500

    const NavBarControl = () => {
        if (window.scrollY > 300) {
            setShowBG(true)
        }
        else {
            setShowBG(false)
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', NavBarControl)
        return () => window.removeEventListener('scroll', NavBarControl)
    }, [])

    return (
        <Fragment>
            {
                showBG ? 
                    <nav className="scrolledNavBar" id="navBar">
                        <ul>
                            <li id="name">Lokesh Pavan Kumar</li>
                            <Link
                                activeClass="active"
                                to="navBar"
                                spy={true}
                                smooth={true}
                                offset={-20000}
                                duration={scrollTime}
                            ><li>Home</li>
                            </Link>
                            <Link
                                activeClass="active"
                                to="aboutSection"
                                spy={true}
                                smooth={true}
                                // offset={-70}
                                duration={scrollTime}
                            ><li>About</li>
                            </Link>
                            <Link
                                activeClass="active"
                                to="educationContainer"
                                spy={true}
                                smooth={true}
                                // offset={-70}
                                duration={scrollTime}
                            ><li>Education</li>
                            </Link>
                            <Link
                                activeClass="active"
                                to="projectsSection"
                                spy={true}
                                smooth={true}
                                offset={-70}
                                duration={scrollTime}
                            ><li>Projects</li>
                            </Link>
                            <Link
                                activeClass="active"
                                to="contactSection"
                                spy={true}
                                smooth={true}
                                // offset={-70}
                                duration={scrollTime}
                            ><li>Contact</li>
                            </Link>                              
                        </ul>
                    </nav>
                :
                    <nav className="navBar">
                        <ul>
                            <Link
                                activeClass="active"
                                to="navBar"
                                spy={true}
                                smooth={true}
                                offset={-700}
                                duration={scrollTime}
                            ><li>Home</li>
                            </Link>
                            <Link
                                activeClass="active"
                                to="aboutSection"
                                spy={true}
                                smooth={true}
                                // offset={-70}
                                duration={scrollTime}
                            ><li>About</li>
                            </Link>
                            <Link
                                activeClass="active"
                                to="educationContainer"
                                spy={true}
                                smooth={true}
                                // offset={-70}
                                duration={scrollTime}
                            ><li>Education</li>
                            </Link>
                            <Link
                                activeClass="active"
                                to="projectsSection"
                                spy={true}
                                smooth={true}
                                // offset={-70}
                                duration={scrollTime}
                            ><li>Projects</li>
                            </Link>
                            <Link
                                activeClass="active"
                                to="contactSection"
                                spy={true}
                                smooth={true}
                                // offset={-70}
                                duration={scrollTime}
                            ><li>Contact</li>
                            </Link>                            
                        </ul>
                    </nav>
            }
        </Fragment>
    );
}

export default NavBar;

