
// The css is written in the Projects css file

const Card = (props) => {

    const { thumbnail, title, domain, startDate, endDate } = props.project

    return (  
        <div className="Card">
            <img src={thumbnail} alt="" />
            <div>
                <h2>{title}</h2>
                <h3>{`Domain: ${domain}`}</h3>
                <h4>{`${startDate} - ${endDate}`}</h4>
            </div>
        </div>
    );
}
 
export default Card;

