import { useEffect, useState } from "react";

const TypeWriter = (props) => {
    const { display_text } = props

    const [index, setIndex] = useState(0)
    const [blink, setBlink] = useState(true)

    const blinker_speed = 500
    const typing_speed = 100

    useEffect(() => {
        if (index === display_text.length) {
            setTimeout(() => {
                setIndex(0)
            }, 5000)
            return
        }

        setTimeout(() => {
        setIndex(index+1)
        }, typing_speed)
    }, [index, display_text])

    useEffect(() => {
        setTimeout(() => {
            blink ? setBlink(false) : setBlink(true)
        }, blinker_speed)
    }, [blink])

    return (
        <h1>{`${display_text.substr(0, index)}${blink ? "|" : " "}`}</h1>
    );
}

export default TypeWriter;
